import {
  UPDATE_KANJI_ORDER,
  UPDATE_KANJI_GROUP,
  UPDATE_KANJI_LEVEL,
} from '../actions';
import { KANJI_ORDERS, KANJI_GROUPS, AVAILABLE_THEMES } from '../constants';

const kanjiSettingDefaultState = {
  order: KANJI_ORDERS.RTK,
  group: KANJI_GROUPS.GRADE,
  level: KANJI_GROUPS.GRADE.defaultLevel,
  theme: AVAILABLE_THEMES.BASE,
};

export default (state = kanjiSettingDefaultState, action) => {
  if (typeof state === 'undefined') return kanjiSettingDefaultState;
  let newState = state;
  switch (action.type) {
    case UPDATE_KANJI_ORDER:
      newState = Object.assign({}, newState, { order: action.order });
      break;
    // Updating kanji group should reset the level automatically.
    case UPDATE_KANJI_GROUP:
      newState = Object.assign({}, newState, { group: action.group });
      newState = Object.assign({}, newState, { level: action.level || action.group.defaultLevel });
      break;
    case UPDATE_KANJI_LEVEL:
      newState = Object.assign({}, newState, { level: action.level || action.group.defaultLevel });
      break;
    default:
      break;
  }
  return newState;
};
