import {
  TAP_SEEN,
  TAP_MASTERED,
  SAVE_NOTES,
} from '../actions';
import StoreManager from './StoreManager';
import {
  LOCAL_STORAGE_CACHE_KEY,
  LOCAL_STORAGE_NOTES_KEY,
} from '../constants';

const MASTERY_ACTIONS = [TAP_SEEN, TAP_MASTERED, SAVE_NOTES];
const LocalStorage = StoreManager(window && window.localStorage);

/**
 * Very simple utility functions to get kanji mastery data.
 */
const updateMasteryData = (lastAction, kanjiMastery) => {
  const { type } = lastAction;
  if (!MASTERY_ACTIONS.includes(type)) return;
  if (type === TAP_SEEN || type === TAP_MASTERED) {
    LocalStorage.setItem(LOCAL_STORAGE_CACHE_KEY, JSON.stringify(kanjiMastery.mastery));
  }

  if (type === SAVE_NOTES) {
    LocalStorage.setItem(LOCAL_STORAGE_NOTES_KEY, JSON.stringify(kanjiMastery.notes));
  }
};

const getFromLocalStorage = key => {
  try {
    const cachedData = LocalStorage.getItem(key);
    const parsedData = JSON.parse(cachedData);
    return parsedData;
  } catch (error) {
    return null;
  }
}

const getMasteryData = () => {
  const cachedData = {
    kanjiMastery: {
      mastery: {},
      notes: {},
    },
  };

  const notesData = getFromLocalStorage(LOCAL_STORAGE_NOTES_KEY);
  const masteryData = getFromLocalStorage(LOCAL_STORAGE_CACHE_KEY);
  if (masteryData && Object.keys(masteryData).length) {
    cachedData.kanjiMastery.mastery = masteryData;
  }

  if (notesData && Object.keys(notesData).length) {
    cachedData.kanjiMastery.notes = notesData;
  }

  return cachedData;
};

export default {
  getMasteryData,
  updateMasteryData,
};
