import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { NO_MASTERY, SEEN, MASTERED } from '../../constants';

export const MASTERY_COLOR = {
  [NO_MASTERY]: null,
  [SEEN]: '#fa9c4f',
  [MASTERED]: '#2cb8e0',
};

const KanjiContainer = styled.div`
  margin: 2px;
  padding: 5px;
  background: ${props => props.masteryColor};
  border-radius: 5px;
`;

const Kanji = ({ kanji, value, mastery }) => {
  return (
    <KanjiContainer
      key={value}
      data-id={value}
      data-kanji={kanji}
      masteryColor={MASTERY_COLOR[mastery]}>
      {kanji}
    </KanjiContainer>
  );
};

Kanji.propTypes = {
  kanji: PropTypes.string,
  id: PropTypes.number,
  mastery: PropTypes.number,
}

export default Kanji;
