import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ModalBase, { ModalContentContainer, ModalContentRow } from './Modal';
import { popModal } from '../../actions';
import styled from 'styled-components';

const BoldLabel = styled.span`
  font-weight: bold;
`;

class RadioOptions extends Component {
  static propTypes = {
    options: PropTypes.oneOfType([
      PropTypes.object,
      PropTypes.array,
    ]),
    onChangeCallback: PropTypes.func,
    selectedKey: PropTypes.string,
  };

  renderOptions = (options, optionCallback) => {
    const listElements = [];
    for (const [key, value] of Object.entries(options)) {
      // NOTE(albert): == on purpose, loose equality for string/number comp
      const label = key == this.props.selectedKey ? <BoldLabel>{value.label}</BoldLabel> : value.label;
      const listElement = (
        <ModalContentRow key={`modal-${key}`}>
          <input
            key={`input-${key}`}
            type="radio"
            value={value.value || key}
            onChange={event => optionCallback(event, value)}
          />
          <label htmlFor={`label-${key}`}>{label}</label>
        </ModalContentRow>
      );
      listElements.push(listElement);
    }
    return <ModalContentContainer>{listElements}</ModalContentContainer>;
  };

  render() {
    const { options, onChangeCallback } = this.props;
    const optionComponents = this.renderOptions(options, onChangeCallback);
    return (
      <ModalBase>
        {optionComponents}
      </ModalBase>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  popModal: () => dispatch(popModal()),
});

const mapStateToProps = () => ({});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(RadioOptions);
