import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import ModalBase from './Modal';
import {
  popModal,
  tapSeen,
  tapMastered,
  saveNotes,
} from '../../actions';
import debounce from 'lodash/debounce';

export const KanjiContentContainer = styled.div`
  height: auto;
  background: ${props => props.theme.base.kanjiContainerBackground};
`;

export const KanjiRow = styled.div`
  width: 100%;
  height: auto;
  text-align: center;
`;

export const KanjiDisplay = styled.div`
  padding: 36px 0;
  font-size: 80px;
  font-weight: bold;
  margin: 0 auto;
`;

export const KanjiOptionRow = styled.div`
  width: calc(100% - 48px);
  padding: 8px 24px;
  display: flex;
  text-align: center;
`;

export const OptionRow = styled.div`
  width: 100%;
  height: 50px;
  text-align: center;
  font-size: 24px;
  flex: 1;

  .modalmoji {
  }

  .modal-value {
    font-weight: 600;
    padding-left: 24px;
    color: hsl(0, 0%, 45%);
  }
`;

export const OptionContent = styled.div`
  padding: 13px 0;
`;

export const LabelContainer = styled.div`
  width: calc(100% - 48px);
  padding: 4px 24px;
`;

export const Label = styled.span`
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-weight: bold;
`;

export const NotesContainer = styled.div`
  width: calc(100% - 48px);
  padding: 4px 24px 24px;
`;

export const NotesTextArea = styled.textarea`
  width: calc(100% - 24px);
  height: 100%;
  font-size: 18px;
  padding: 12px;
  border: 1px solid #2cb8e0; //${props => props.theme.base.borderColor};
`;

class KanjiModal extends Component {
  static propTypes = {
    kanji: PropTypes.string,
    /* NOTE: this id is a number, but got casted of as a string */
    id: PropTypes.string,
    notes: PropTypes.string,

    /* FUTURE PROPS */
    seen: PropTypes.bool,
    mastered: PropTypes.bool,
    /* FUTURE PROPS */
  };

  state = {
    notes: this.props.notes,
    notesOpen: false,
  };

  /**
   * Clicking the more info button replaces the OptionContents with more information
   * 1) Fetch information
   * 2) Displays them
   *
   * TODO: Need to display more information under kanji
   * <button /> - IE6 problems, implicitly submits
   */
  tapAndExitModal = optionCallback => value => {
    optionCallback(value);
    this.props.popModal();
  }

  toggleTextArea = () => {
    this.setState({
      notesOpen: !this.state.notesOpen,
    });
  }

  updateNotes = event => {
    const notes = event.target.value;
    this.setState({ notes });
    this.updateNotesInternal(this.props.kanji, notes);
  }

  // TODO: rename
  updateNotesInternal = debounce((kanjiId, notes) => {
    this.props.saveNotes(kanjiId, notes);
  }, 175);

  renderSeen = () => {
    const tapSeenAndExit = this.tapAndExitModal(this.props.tapSeen);
    return (
      <OptionRow>
        <OptionContent>
          <span
            className='modalmoji'
            onClick={() => tapSeenAndExit(this.props.kanji)}>
            👀
          </span>
        </OptionContent>
      </OptionRow>
    );
  }

  renderMastered = () => {
    const tapMasteredAndExit = this.tapAndExitModal(this.props.tapMastered);
    return (
      <OptionRow>
        <OptionContent>
          <span
            className='modalmoji'
            onClick={() => tapMasteredAndExit(this.props.kanji)}>
            🤓
          </span>
        </OptionContent>
      </OptionRow>
    );
  }

  renderInfo = () => {
    return (
      <OptionRow>
        <OptionContent>
          <span className='modalmoji'>💁🏽‍♂️</span>
        </OptionContent>
      </OptionRow>
    );
  }

  renderNotes = () => {
    return (
      <OptionRow>
        <OptionContent>
          <span
            className='modalmoji'
            onClick={this.toggleTextArea}>
            ✏️
          </span>
        </OptionContent>
      </OptionRow>
    );
  }

  renderNotesTextArea = () => {
    const { notes, notesOpen } = this.state;
    if (!notesOpen) return null;

    return (
      <React.Fragment>
        <LabelContainer>
          <Label>
            Notes
          </Label>
        </LabelContainer>
        <NotesContainer>
          <NotesTextArea
            placeholder={'notes about this kanji?'}
            value={notes}
            onChange={event => this.updateNotes(event)}
          >
          </NotesTextArea>
        </NotesContainer>
      </React.Fragment>
    );
  }

  render() {
    return (
      <ModalBase>
        <KanjiContentContainer>
          <KanjiRow>
            <KanjiDisplay>
              {this.props.kanji}
            </KanjiDisplay>
          </KanjiRow>
          <KanjiOptionRow>
            {this.renderSeen()}
            {this.renderMastered()}
            {this.renderNotes()}
          </KanjiOptionRow>
          {this.renderNotesTextArea()}
        </KanjiContentContainer>
      </ModalBase>
    );
  }
}

const mapStateToProps = () => ({});
const mapDispatchToProps = dispatch => ({
  tapSeen: kanji => dispatch(tapSeen(kanji)),
  tapMastered: kanji => dispatch(tapMastered(kanji)),
  saveNotes: (kanji, notes) => dispatch(saveNotes(kanji, notes)),
  popModal: () => dispatch(popModal()),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(KanjiModal);
