// DEFAULT FEATURES
export const RECEIVE_ERROR = 'RECEIVER_ERROR_FROM_BACKEND';
export const UPDATE_KANJI_ORDER = 'TOGGLE_KANJI_ORDERING';
export const UPDATE_KANJI_GROUP = 'UPDATE_KANJI_GROUPING';
export const UPDATE_KANJI_LEVEL = 'UPDATE_KANJI_LEVEL';
export const TAP_SEEN = 'TAP_SEEN_ON_KANJI';
export const TAP_MASTERED = 'TAP_MASTERED_ON_KANJI';
export const TAP_INFO = 'SEE_MORE_INFO_ON_KANJI';
export const ADD_MODAL = 'ADD_A_NEW_DIALOG';
export const POP_MODAL = 'POP_OLD_DIALOG_OFF';
export const SAVE_NOTES = 'SAVE_KANJI_NOTES';
// FUTURE FEATURES //
export const LOG_IN = 'USER_LOGGED_IN';
export const LOG_OUT = 'USER_LOGGED_OUT';
export const CHANGE_THEME = 'CHANGE_BETWEEN_DIFFERENT_THEMES';
export const RESET_ALL_PROGRESS = 'RESET_ALL_PROGRESS';
// FUTURE FEATURES //

function lastUpdated(actionObject) {
  return Object.assign(actionObject, { timestamp: Date.now() });
}

export function tapSeen(kanji) {
  return lastUpdated({
    kanji,
    type: TAP_SEEN,
  });
}

export function tapMastered(kanji) {
  return lastUpdated({
    kanji,
    type: TAP_MASTERED,
  });
}

export function saveNotes(kanji, notes) {
  return lastUpdated({
    notes,
    kanji,
    type: SAVE_NOTES,
  });
}

export function changeKanjiOrdering(order) {
  return {
    order,
    type: UPDATE_KANJI_ORDER,
  };
}

export function changeKanjiGrouping(group) {
  return {
    group,
    type: UPDATE_KANJI_GROUP,
  };
}

export function changeKanjiLevel(level) {
  return {
    level,
    type: UPDATE_KANJI_LEVEL,
  };
}

function createModalObject(modalType, extraProps={}) {
  return {modalType, extraProps};
}

export function addModal(modalType, extraProps) {
  return {
    modal: createModalObject(modalType, extraProps),
    type: ADD_MODAL,
  };
}

export function popModal() {
  return {
    type: POP_MODAL,
  };
}
