/*
 * Kanji starter object:
 * { k1: {s1: 1, s2: 2}, k2: ...}
 */
export const sortByKeyInsideObj = (obj, subkey) => {
  return (k1, k2) => {
    if (obj[k1][subkey] < obj[k2][subkey]) return -1;
    if (obj[k1][subkey] > obj[k2][subkey]) return 1;
    return 0;
  };
};

/*
 * Iterate through array, increments at a time.
 */
export const makeArrayGenerator = function* (array, increment) {
  let counter = 0;
  while (counter < array.length) {
    yield array.slice(counter, counter + increment);
    counter = counter + increment;
  }
};

/*
 * Filter out from master object.
 */
export const filterFuncFromObj = (obj, subkey, func) => key => func(obj[key][subkey]);

/**
 * Calculate fullness.
 */
export const calculateFullness = () => null;

/**
 * Get total number of kanji. We can simply cache this value.
 */
export const getKanjiTotal = (kanjiData, grouping, currentGroup) => {
  let total = 0;
  for (const [, value] of Object.entries(kanjiData)) {
    if (value[grouping] === currentGroup) {
      total = total + 1;
    }
  }
  return total;
}
