import {
  ADD_MODAL,
  POP_MODAL,
} from '../actions';

// NOTE: Prevents adding another of the same modal type
const addModalInternal = (modalList, modal) => {
  if (modalList.every(existingModal => modal.modalType !== existingModal.modalType)) {
    modalList.push(modal);
  }
};

const modalDefaultState = {
  modalList: [],
};

export default (state = modalDefaultState, action) => {
  if (typeof state === 'undefined') return modalDefaultState;
  const modalList = [...state.modalList];

  switch (action.type) {
    case ADD_MODAL:
      addModalInternal(modalList, action.modal);
      break;
    case POP_MODAL:
      modalList.pop();
      break;
    default:
      break;
  }
  return { modalList };
};
