import { connect } from 'react-redux';
import ChangeLevel from './ChangeLevel';
import { changeKanjiLevel } from '../../actions';

const mapStateToProps = state => ({
  currentLevel: state.kanjiSetting.level,
});

const mapDispatchToProps = dispatch => ({
  onKanjiLevelChange: level => dispatch(changeKanjiLevel(level)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ChangeLevel);
