import React, { Component } from 'react';
import styled from 'styled-components';
import { MODAL_TYPES } from '../../constants';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const Container = styled.div`
  width: 100%;
  padding: 36px 0 18px 0;
`;

const NavContents = styled.div`
  margin: 0 auto;
  width: 100%;
  display: flex;
`;

const LogoContainer = styled.div`
  font-size: 36px;
  font-weight: 700;
  margin-left: 36px;
  align-self: flex-start;
  flex: 3;
`;

const SettingsContainer = styled.div`
  margin-right: 36px;
  align-self: flex-end;
  flex: 1;
  display: flex;
  flex-direction: row-reverse;
`;

const SettingsButton = styled.button`
  padding: 0;
  border: none;
  background: none;
`;

/*
 * When push we add new content to the modalContent
 * Modal detects change and will activate, so actually modal lives on the base app.
 */

class TopNav extends Component {
  // TODO: make global settings shown state
  showSettings = () => {
    this.props.addModal(MODAL_TYPES.SETTINGS, {});
  }

  render() {
    return (
      <Container>
        <NavContents>
          <LogoContainer><strong>感字</strong></LogoContainer>
          <SettingsContainer>
            <SettingsButton onClick={this.showSettings}>
              <FontAwesomeIcon
                icon="cog"
                size="2x"
              />
            </SettingsButton>
          </SettingsContainer>
        </NavContents>
      </Container>
    );
  }
}

export default TopNav;
