import React, { Component } from 'react';
import styled from 'styled-components';
import { MASTERY_COLOR } from '../kanji-grid/Kanji';
import kanjiData from '../../data/starter_kanji_data.json';
import { getKanjiTotal } from '../../utils';
import { SEEN, MASTERED } from '../../constants';

const Container = styled.div`
  width: calc(100% - 72px);
  height: auto;
  padding: 16px 10px;
  margin: 10px 26px;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  text-align: center;
  font-size: 18px;
  color: #ecaaaa; // TODO
  border: 1px solid #ecaaaa;
  justify-content: space-between;
`;

const ContainerItem = styled.div`
  flex: ${props => props.flexGrow};
  display: flex;
  justify-content: flex-end;
`;

const StatBar = styled.div`
  width: 20%;
  height: 100%;
  border-radius: 5px;
  display: inline-block;
  // HACK: TODO: Wtf is this LOL
  background: ${props => props.color}61;
  margin: 0 1px;
  opacity: 0.55;
`;

const StatBarInner = styled.div`
  width: ${props => props.fillPtg}%;
  height: 100%;
  background: ${props => props.color};
  border-radius: 5px;
`;

/*
 * NOTE: This component should be the one outputing the different kanji containers.
 */
class ChangeLevel extends Component {
  renderMasteryBars = () => {
    const { currentGroup, currentLevel, mastery } = this.props;
    const { kanjiField } = currentGroup;
    const currentLevelFieldValue = currentLevel.value;

    const totalKanji = getKanjiTotal(kanjiData, kanjiField, currentLevelFieldValue);
    let totalSeen = 0;
    let totalMastered = 0;
    for (const [key, value] of Object.entries(mastery)) {
      // TODO: find a way to safely access
      const kanjiFieldValue = kanjiData[key] && kanjiData[key][kanjiField];
      if (kanjiFieldValue !== currentLevelFieldValue) continue;
      if (value === SEEN) {
        totalSeen += 1;
      }

      if (value === MASTERED) {
        totalSeen += 1;
        totalMastered += 1;
      }
    }

    const totalSeenPtg = totalSeen / totalKanji * 100;
    const totalMasteredPtg = totalMastered / totalKanji * 100;
    const bars = [];
    bars.push(this.renderBar(MASTERY_COLOR[SEEN], SEEN, totalSeenPtg));
    bars.push(this.renderBar(MASTERY_COLOR[MASTERED], MASTERED, totalMasteredPtg))
    return bars;
  }

  renderBar = (color, key, barFillPtg = 100) => {
    return (
      <StatBar color={color} key={key}>
        <StatBarInner
          color={color}
          fillPtg={barFillPtg}
        />
      </StatBar>
    );
  }

  render() {
    const { currentLevel } = this.props;
    return (
      <React.Fragment>
        <Container>
          <ContainerItem
          >
            {currentLevel.label}
          </ContainerItem>
          <ContainerItem
            flexGrow={2}
          >
            {this.renderMasteryBars()}
          </ContainerItem>
        </Container>
      </React.Fragment>
    );
  }
}

export default ChangeLevel;
