import {
  TAP_SEEN,
  TAP_MASTERED,
  SAVE_NOTES,
} from '../actions';
import { SEEN, MASTERED } from '../constants';

const kanjiMasteryDefaultState = {
  /**
   * mastery = {
   *  id1: SEEN,
   *  id2: MASTERED,
   *  ...
   * }
   */
  mastery: {},
  notes: {},
};

/**
 * Return the correct branch
 */
export default (state = kanjiMasteryDefaultState, action) => {
  if (typeof state === 'undefined') return kanjiMasteryDefaultState;
  let { mastery, notes } = state;
  let untapping = false;
  switch (action.type) {
    case TAP_SEEN:
      untapping = mastery[action.kanji] === SEEN;
      mastery = Object.assign({}, mastery, { [action.kanji]: SEEN });
      if (untapping) delete mastery[action.kanji];
      break;
    case TAP_MASTERED:
      untapping = mastery[action.kanji] === MASTERED;
      mastery = Object.assign({}, mastery, { [action.kanji]: MASTERED });
      if (untapping) delete mastery[action.kanji];
      break;
    case SAVE_NOTES:
      const kanjiNotes = action.notes;
      notes = Object.assign({}, notes, { [action.kanji]: kanjiNotes });
      break;
    default:
      break;
  }
  return { mastery, notes };
};
