import React, { Component } from 'react';
import styled from 'styled-components';
// TODO: this should be lazy loaded
import kanjiData from '../../data/starter_kanji_data.json';
import {
  sortByKeyInsideObj,
  filterFuncFromObj,
} from '../../utils';
import Kanji from './Kanji';
import {
  MODAL_TYPES,
  CHOICES,
  NO_MASTERY,
  KANJI_BOX_SIZE,
  KANJI_BOX_MULTIPLIER
} from '../../constants';
import debounce from 'lodash/debounce';

/**
 * Look at ゴーシャO and U, pretty cool font. Or Gosha OS
 */
const Container = styled.div`
  width: calc(100% - 72px);
  height: auto;
  max-height: ${props => props.maxHeight || 300}px;
  overflow: scroll;
  padding: 10px;
  margin: 26px;
  border-radius: 10px;
  background: ${props => props.theme.base.kanjiContainerBackground};
  display: grid;
  text-align: center;
  justify-content: space-evenly;
  grid-template-columns: repeat(${props => props.columnSize || 8}, 1fr);
  border: 1px solid ${props => props.theme.base.borderColor};
`;

/*
 * NOTE: This component should be the one outputing the different kanji containers.
 * Look into requestAnimationFrame vs debounce
 */
class KanjisContainer extends Component {
  state = {
    width: 0,
    height: 0,
  };

  componentDidMount() {
    // Move to top level component
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  // We need this to trigger rerender
  updateWindowDimensions = debounce(() => {
    this.setState({
      width: window.innerWidth,
      height: window.innerHeight,
    })
  }, 88);

  determineRowSize = () => {
    return parseInt(window.innerWidth / (KANJI_BOX_SIZE * KANJI_BOX_MULTIPLIER));
  }

  determineMaxHeight = () => {
    return 0.42 * window.innerHeight;
  }

  renderKanji = key => {
    const id = kanjiData[key].id;
    const mastery = this.props.mastery[key] || NO_MASTERY;
    return (
      <Kanji
        key={id}
        kanji={key}
        value={id}
        mastery={mastery}
      />
    );
  }

  kanjiOnClick = event => {
    if (!event.target ||
      !event.target.dataset ||
      !event.target.dataset.kanji ||
      !event.target.dataset.id) return;
    const notes = this.props.notes[event.target.dataset.kanji];
    this.props.addModal(MODAL_TYPES.KANJI_MODAL, { ...event.target.dataset, notes });
  }

  secondaryKanjiFilter = level => value => {
    if (!level || level === CHOICES.SHOW_ALL.value) return true;
    return value === level;
  }

  render() {
    // Event handler on the kanji container, not individual kanji
    const { currentGroup, currentOrder, currentLevel } = this.props;
    const { kanjiField, defaultLevel } = currentGroup;
    const customSorter = sortByKeyInsideObj(kanjiData, currentOrder.kanjiField);
    const secondaryFilter = this.secondaryKanjiFilter((currentLevel && currentLevel.value) || defaultLevel.value);
    const filterFunc = filterFuncFromObj(kanjiData, kanjiField, secondaryFilter);
    const kanjisToDisplay = Object.keys(kanjiData).filter(filterFunc).sort(customSorter).map(this.renderKanji);

    return (
      <Container
        onClick={this.kanjiOnClick}
        maxHeight={this.determineMaxHeight()}
        columnSize={this.determineRowSize()}
      >
        {kanjisToDisplay}
      </Container>
    );
  }
}

export default KanjisContainer;
